@import '../../assets/scss/variable';
@import '../../assets/scss/mixin';

.skill {
  $this: '.skill';
  border-radius: #{$unit * .5};
  box-shadow: 0 0 0 #{$unit * .2} $color-black-20;
  width: auto;
  background: $color-light-1;
  padding: #{$unit * .5} $unit;
  cursor: pointer;
  margin: #{$unit * .2};
  @include transition(.1s);

  & &__label{
    @include transition(.2s);
    color: $color-primary;
  }

  // States
  &--selected{
    box-shadow: 0 0 0 #{$unit * .2} $color-secondary;

    #{$this}__label{
      color: $color-secondary;
    }
  }
  &--disabled{
    cursor: default;
    box-shadow: 0 0 0 #{$unit * .2} $color-primary;
  }
}
