@import '../../assets/scss/variable';
@import '../../assets/scss/mixin';

.button {
  $this: '.button';
  padding: #{$unit * 1.5};
  background: $color-dark-1;
  border-radius: #{$unit * .5};
  cursor: pointer;
  @include flex;
  @include flex-align-items(center);
  @include flex-justify-content(center);

  &__label {
    & + #{$this}__icon{
      color: $color-light-1;
      margin-left: #{$unit};
    }
  }
 
  // /Circle/action icon
  &--icon-only{
    max-width: #{$unit * 3.6};
    min-width: #{$unit * 3.6};
    width: #{$unit * 3.6};
    max-height: #{$unit * 3.6};
    min-height: #{$unit * 3.6};
    height: #{$unit * 3.6};
    padding: 0;
    border-radius: #{$unit * 1.8};
    @include media($tablet){
      max-width: #{$unit * 2.8};
      min-width: #{$unit * 2.8};
      width: #{$unit * 2.8};
      max-height: #{$unit * 2.8};
      min-height: #{$unit * 2.8};
      height: #{$unit * 2.8};
      border-radius: #{$unit * 1.4};
    }
  }

  // Variants
  &--dark{
    background: $color-black-10;

    #{$this}__label{
      color: $color-light-1;
    }
    #{$this}__icon{
      color: $color-light-1;
    }
  }
  &--tertiary{
    background: $color-black-05;

    #{$this}__label{
      color: $color-primary;
    }
    #{$this}__icon{
      color: $color-primary;
    }
  }
  &--secondary{
    background: transparent;
    border: #{$unit * .2} solid $color-secondary;

    #{$this}__label{
      color: $color-secondary;
    }
    #{$this}__icon{
      color: $color-secondary;
    }
  }
  &--cta{
    background: $color-secondary;

    #{$this}__label{
      color: $color-light-1;
    }
    #{$this}__icon{
      color: $color-light-1;
    }
  }

  // States
  &--disabled{
    cursor: not-allowed;
    opacity: .5;
  }
}
