@import '../../assets/scss/variable';
@import '../../assets/scss/mixin';

.header {
  $this: '.header';
  padding: #{$unit * 2} 0;
  @include media($tablet){
    padding: #{$unit} 0;
  }
  
  &__container{
    @include flex;
    @include flex-align-items(center);  
    @include flex-justify-content(space-between);
  }
  &__left{
    @include flex;
    @include flex-align-items(center);  
  }
  &__back{
    margin-right: #{$unit};
  }
}
