@import '../../assets/scss/variable';
@import '../../assets/scss/mixin';

.medal {
  $this: '.medal';
  font-size: 32px;
  color: $color-primary;
  position: relative;
  @include media($tablet){
    font-size: 28px;
  }

  &__position{
    position: absolute;
    top: 50%;
    left: 50%;
    @include transform-translate(-50%, -50%);
  }

  // Positions
  &--1{
    color: $color-success;
  }
}
