@import '../../assets/scss/variable';
@import '../../assets/scss/mixin';

.statistic {
  $this: '.statistic';
  @include flex;
  @include flex-align-items(center);
  margin-top: #{$unit * 1};
  @include media($tablet){
    margin-top: #{$unit * .5};
  }

  &__icon{
    margin-right: $unit;
    width: #{$unit * 2};
    text-align: center;
    font-size: 24px;
    @include media($tablet){
      width: #{$unit * 1.5};
      font-size: 18px;
    }
  }

  // Types
  &--positive{
    #{$this}__icon, #{$this}__copy{
      color: $color-success;
    }
  }
  &--neutral{
    #{$this}__icon, #{$this}__copy{
      color: $color-neutral;
    }
  }
  &--negative{
    #{$this}__icon, #{$this}__copy{
      color: $color-negative;
    }
  }
  &--strengths{
    #{$this}__icon, #{$this}__copy{
      color: $color-primary;
    }
  }
}
