@import '../../assets/scss/variable';
@import '../../assets/scss/mixin';

.row{
  $this: '.row';
  @include flex;
  @include flex-align-items(flex-start);
  @include flex-wrap(wrap);

  // Free flow (skills)
  &--flow{
    #{$this}__column{
      width: auto;
    }
  }

  // Spacing
  &--gutters-xsmall{
    margin: -#{$unit * .25};

    & > #{$this}__column{
      padding: #{$unit * .25};

      &--group{
        padding: 0;

        & > #{$this}__column{
          padding: #{$unit * .25};
        }
      }
    }
  }
  &--gutters-small{
    margin: -#{$unit * .5};

    & > #{$this}__column{
      padding: #{$unit * .5};

      &--group{
        padding: 0;

        & > #{$this}__column{
          padding: #{$unit * .5};
        }
      }
    }
  }
  &--gutters{
    margin: -#{$unit};

    & > #{$this}__column{
      padding: #{$unit};
      
      &--group{
        padding: 0;

        & > #{$this}__column{
          padding: #{$unit};
        }
      }
    }
  }
  &--gutters-large{
    margin: -#{$unit * 2};

    & > #{$this}__column{
      padding: #{$unit * 2};

      &--group{
        padding: 0;

        & > #{$this}__column{
          padding: #{$unit * 2};
        }
      }
    }
  }

  // Positioning
  &--space-between{
    @include flex-justify-content(space-between);
  }
  &--justify-end{
    @include flex-justify-content(flex-end);
  }
  &--stretch{
    @include flex-align-items(stretch);

    & > #{$this}__column{
      @include flex;
    }
  }
  @include media($tablet){
    &--tablet{
      &-justify-start{
        @include flex-justify-content(flex-start);
      }
      &-justify-end{
        @include flex-justify-content(flex-end);
      }
    }
  }

  // Columns
  &__column{
    width: 100%;
    
    &--20{
      width: 20%;
    }
    &--25{
      width: 25%;
    }
    &--33{
      width: 33.33%;
    }
    &--50{
      width: 50%;
    }
    &--66{
      width: 66.66%;
    }
    &--75{
      width: 75%;
    }
    &--80{
      width: 80%;
    }
    @include media($tablet){
      &--tablet{
        &-20{
          width: 20%;
        }
        &-25{
          width: 25%;
        }
        &-33{
          width: 33.33%;
        }
        &-50{
          width: 50%;
        }
        &-66{
          width: 66.66%;
        }
        &-75{
          width: 75%;
        }
        &-80{
          width: 80%;
        }
        &-100{
          width: 100%;
        }
      }
    }
    @include media($mobile){
      &--mobile{
        &-20{
          width: 20%;
        }
        &-25{
          width: 25%;
        }
        &-33{
          width: 33.33%;
        }
        &-50{
          width: 50%;
        }
        &-66{
          width: 66.66%;
        }
        &-75{
          width: 75%;
        }
        &-80{
          width: 80%;
        }
        &-100{
          width: 100%;
        }
      }
    }

    // Group 
    &--group{
      padding: 0;
      @include flex;
      @include flex-wrap(wrap);
    }

    // Alignment
    &--align-right{
      @include flex;
      @include flex-justify-content(flex-end);
    }
    @include media($tablet){
      &--tablet{
        &-align-right{
          @include flex;
          @include flex-justify-content(flex-end);
        }
        &-align-left{
          @include flex;
          @include flex-justify-content(flex-start);
        }
      }
    }
    @include media($mobile){
      &--mobile{
        &-align-right{
          @include flex;
          @include flex-justify-content(flex-end);
        }
        &-align-left{
          @include flex;
          @include flex-justify-content(flex-start);
        }
      }
    }
  }
}