@import '../../assets/scss/variable';
@import '../../assets/scss/mixin';

.hult-intro {
  $this: '.hult-intro';
  overflow: hidden;
  
  &__video{
    margin: -#{$unit * 2} -#{$unit * 2} 0;
    width: calc(100% + #{$unit * 4});
    height: #{$unit * 22.3};
    @include media($tablet){
      width: calc(100% + #{$unit * 3});
      height: #{$unit * 12.1};
      margin: -#{$unit * 1.5} -#{$unit * 1.5} 0;
    }
  }
}
