@import '../../assets/scss/variable';
@import '../../assets/scss/mixin';

.degree {
  $this: '.degree';
  overflow: hidden;
  margin: $unit 0;
  @include media($tablet){
    margin: #{$unit * .5} 0;
  }

  &__header{
    position: relative;
    padding: #{$unit * 2};
    margin: -#{$unit * 2};
    cursor: pointer;
    @include flex;
    @include flex-align-items(center);
    @include flex-justify-content(space-between);
    @include media($tablet){
      padding: #{$unit * 1.5};
      margin: -#{$unit * 1.5};
    }

    &-left{
      @include flex;
      @include flex-align-items(center);
    }
  }
  &__icon{
    color: $color-primary;
    font-size: 24px;
    @include transition(.2s);
    @include media($tablet){
      font-size: 20px;
    }

    &--open{
      @include transform-rotate(180deg);
    }
  }
  &__title{
    margin-left: #{$unit};
    color: $color-primary;
  }
  &__content{
    overflow: hidden;
    display: none;
    margin-top: #{$unit * 2};
    @include media($tablet){
      margin-top: #{$unit * 1.5};
      // padding: 0 $unit $unit;
    }

    &--open{
      display: block;
    }
  }
}
